/* стилизация кнопки меню */
/* *********************** */
.main-nav__toggle {
  border: none;
  padding: 0;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  position: absolute;
  z-index: 4;
  top: 23px;
  right: 20px;
  width: 50px;
  height: 24px;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 43px;
  }
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle span {
  display: block;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  color: transparent;
  font-size: 1px;
}
.main-nav__toggle span::before {
  content: "";
  position: absolute;
  top: 0;
  display: block;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav__toggle span::after {
  content: "";
  position: absolute;
  bottom: 0;
  display: block;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle {
  width: 23px;
  height: 23px;
}
.main-nav--opened .main-nav__toggle span {
  display: block;
  width: 23px;
  height: 0;
  background-color: #ffffff;
  color: transparent;
  font-size: 1px;
}
.main-nav--opened .main-nav__toggle span::before {
  width: 32px;
  top: 50%;
  transform: translateY(-50%) translateX(-4px) rotate(45deg);
}
.main-nav--opened .main-nav__toggle span::after {
  width: 32px;
  bottom: 50%;
  transform: translateY(50%) translateX(-4px) rotate(-45deg);
}
.main-nav__toggle:hover span,
.main-nav__toggle:hover span::before,
.main-nav__toggle:hover span::after {
  background-color: #d22856;
}
.main-nav__toggle:active span,
.main-nav__toggle:active span::before,
.main-nav__toggle:active span::after {
  background-color: #d22856;
  opacity: 0.3;
}
